    export default {
        props: {
            defaultIndex: {
                type: Number,
                default: 0
            },
            hideRightArrow: {
                type: Boolean,
                default: false
            },
            valueStyle: {
                type: String,
                default: ''
            },
			fontStyle: {
			    type: String,
			    default: ''
			},
            placeholder: {
                type: String,
                default: '请选择'
            },
            items: {
                type: Array,
                default: [{
                    label: 'label1',
                    value: '',
                    subValue: ''
                }]
            },
            other: {
                type: [Object, Number, String, Array, Boolean],
                default: ''
            }
        },
        watch: {
            defaultIndex() {
                this.current = this.defaultIndex;
            }
        },
        mounted() {
            this.current = this.defaultIndex;
        },
        data() {
            return {
                current: 0
            }
        },
        methods: {
            clickItem(index) {
                this.current = index;
                this.$emit('clickItem', this.items[index]);
            },
        },
    }
